<template>
  <div class="text-content-wrapper" :class="{'inline': inline}">
    <p v-html="contentText" :class="[size]"></p>
    <slot></slot>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      text: {
        type: String,
        default() {
          return ''
        }
      },
      inline: {
        default: false
      },
      size: {
        default: 'middle'
      }
    },
    data() {
      return {
        faces: [{id: '01', name: '#()'}, {id: '02', name: '#()'}, {id: '03', name: '#()'}, {id: '04', name: '#()'}, {id: '05', name: '#()'}, {id: '06', name: '#()'}, {id: '07', name: '#()'}, {id: '08', name: '#()'}, {id: '09', name: '#()'}, {id: '10', name: '#()'}, {id: '11', name: '#()'}, {id: '12', name: '#()'}, {id: '13', name: '#()'}, {id: '14', name: '#()'}, {id: '15', name: '#()'}, {id: '16', name: '#()'}, {id: '17', name: '#()'}, {id: '18', name: '#()'}, {id: '19', name: '#()'}, {id: '20', name: '#()'}, {id: '21', name: '#()'}, {id: '22', name: '#()'}, {id: '23', name: '#()'}, {id: '24', name: '#()'}, {id: '25', name: '#()'}, {id: '26', name: '#()'}, {id: '27', name: '#()'}, {id: '28', name: '#()'}, {id: '29', name: '#()'}, {id: '30', name: '#()'}, {id: '31', name: '#()'}, {id: '32', name: '#()'}, {id: '33', name: '#()'}]
      }
    },
    computed: {
      contentText() {
        let currentText = this.text
        const face = this.faces
        const l = face.length
        for (let i = 0; i < l; i++) {
          const id = face[i].id
          const faceName = face[i].name
          if (currentText.indexOf(faceName) !== -1) {
            const img = '<img src="./static/images/face/fFace_' + id + '.png"/>'
            const rep = faceName.replace(/#|\(|\)/g, '')
            currentText = currentText.replace(new RegExp('#\\(' + rep + '\\)', 'g'), img)
          }
          if (i === l - 1) {
            return currentText
          }
        }
      }
    }
  }
</script>

<style lang="scss">
  .text-content-wrapper{
    p{
      display: inline-block;
      line-height: 1.5;
      font-size: 16px;
      color: #555;
      img{
        width: 26px;
        margin-bottom: -6px;
      }
      &.small{
        font-size: 14px;
        img{
          width: 16px;
          margin-bottom: -2px;
        }
      }
    }
    &.inline{
      display: inline-block;
    }
  }
</style>



