import axios from 'axios'

/**
 * [requestGet get]
 * @param {[string]} url []
 * @return {[object]}    [promise]
 */
export function requestGet(url) {
  return axios.get(url)
    .then((res) => {
    return Promise.resolve(res.data)
  })
}

/**
 * [requestPost post]
 * @param {[string]} url []
 * @param {[object]} data []
 * @return {[object]}    [promise]
 */
export function requestPost(url, data) {
  return axios.post(url, data)
    .then((res) => {
    return Promise.resolve(res.data)
  })
}



